body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    color: #d8d9da;
}

html {
    background: -webkit-radial-gradient(at top right, #48acc7 0%, #1a3850 100%);
    background: -o-radial-gradient(at top right, #48acc7 0%, #1a3850 100%);
    background: radial-gradient(at top right, #48acc7 0%, #1a3850 100%);
}

html, body, .overflow-wrapper {
    overflow-x: hidden;
}


.moon {
    position: absolute;
    top: -40px;
    right: 0;
    z-index: -1;
}

.rocket {
    position: absolute;
    left: 0px;
    bottom: 0px;
    z-index: -1;
}

.fire {
    position: absolute;
    left: 0px;
    bottom: 0px;
    z-index: -1;
}

.navbar-brand {
    font-family: 'Permanent Marker', cursive;
}

.navbar-brand small {
    display:block;
    font-size:14px;
    margin-top: -11px;
    color: #F3969A;
}

.navbar-dark .navbar-nav .nav-link {
    color: #f3969a;
}

.navbar-dark .navbar-nav .nav-link:hover {
    color: #ef7479;
}

.faq h4 {
    color: #d8d9da;
}

input {
    width: 100%;
    padding: 10px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    background: none;
    outline: none;
    resize: none;
    border: 0;
    font-family: 'Montserrat', sans-serif;
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
    border-bottom: 2px solid #bebed2
}

input:focus {
    border-bottom: 2px solid #78788c
}

.bg-moon {
    background: -webkit-linear-gradient(55deg, rgba(197, 79, 69, 0.29) 0%, rgba(197, 38, 25, 0.13) 100%);
    background: -o-linear-gradient(55deg, rgba(197, 79, 69, 0.29) 0%, rgba(197, 38, 25, 0.13) 100%);
    background: linear-gradient(35deg, rgba(197, 79, 69, 0.29) 0%, rgba(197, 38, 25, 0.13) 100%);
}

.jumbotron {
    background: -webkit-linear-gradient(55deg, rgb(231, 231, 231) 0%, rgba(253, 253, 253) 100%);
    background: -o-linear-gradient(55deg, rgb(231, 231, 231) 0%, rgba(253, 253, 253) 100%);
    background: linear-gradient(35deg, rgb(231, 231, 231) 0%, rgba(253, 253, 253) 100%);
}

@media (min-width: 576px) {
    .jumbotron {
        padding: 2rem 1rem;
    }
}
    
.Select.is-disabled > .Select-control {
    background-color: transparent;
}

.input-group-text {
    background: transparent;
}

.card {
    color: black;
}


