/* Sticky footer styles
-------------------------------------------------- */
html {
  position: relative;
  min-height: 100%;
}

body {
  /* Margin bottom by footer height */
  margin-bottom: 60px;
  background: transparent;
  /*font-family: 'Bubblegum Sans', cursive;*/
  font-family: 'Roboto', sans-serif;
  /*font-size: 1.2em;*/
}

.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  /* Set the fixed height of the footer here */
  height: 60px;
  line-height: 60px; /* Vertically center the text there */
  background-color: #f5f5f5;
}

/* Custom page CSS
-------------------------------------------------- */
/* Not required for template or sticky footer method. */

main.container {
  padding: 40px 15px 0;
}

.footer > .container {
  padding-right: 15px;
  padding-left: 15px;
}

code {
  font-size: 80%;
}

.split-para {
  display: block;
  margin: 10px;
}
.split-para span {
  display: block;
  float: right;
  width: 50%;
  margin-left: 10px;
}

.shadow {
  -webkit-box-shadow: -5px 5px 11px 1px #48acc7;
          box-shadow: -5px 5px 11px 1px #48acc7;
}

.blink_me {
  -webkit-animation: blinker 1s linear infinite;
          animation: blinker 1s linear infinite;
}

caption {
  caption-side: top;
}

.introjs-helperNumberLayer {
  background: -webkit-gradient(linear, left top, left bottom, from(#F3969A), to(#F3969A));
  background: -webkit-linear-gradient(top, #F3969A 0%, #F3969A 100%);
  background: -o-linear-gradient(top, #F3969A 0%, #F3969A 100%);
  background: linear-gradient(to bottom, #F3969A 0%, #F3969A 100%);
}

.navbar-brand {
  font-size: 2.25rem;
}


